* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
  display: flex;
  align-items: center;
}
.country_name{
  font-weight: 700;
  font-size: 24px;
  margin: 0px 0px 8px 10px; 

}
.country-item{
  list-style: none;
}
.information-item{
  font-weight: 500;
    font-size: 18px;
    
}

.languages_item{
  list-style: square;
  font-weight: 700;
}
.information {
  font-weight: 700;
}